.cart {
  @media (max-width: 767.98px) {
    margin: 0 auto;
  }

  &__photo-block {
    width: 100%;
    height: 222px;
    border-radius: 10px 10px 0px 0px;
    max-width: 360px;
  }

  &__photo {
    width: 100%;
    height: 222px;
    position: relative;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 4px;
      transition: 0.5s;

      &:hover {
        -webkit-filter: grayscale(100%);
        filter: grayscale(100%);
        transform: scale(1.1);
      }
    }
  }

  &__description {
    border-radius: 10px;
    border: 1px solid #E7E6E6;
    height: 253px;
    background: #FFF;
    padding: 23px 24px;
    margin-top: -11px;
    position: relative;
    z-index: 1;
    max-width: 360px;
    @media (max-width: 767.98px) {
      padding: 27px 16px;
      height: auto;
    }

    .title {
      color: #0B0B0B;
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      margin-bottom: 16px;
      display: block;
      display: -webkit-box;
      overflow: hidden;
      min-height: 44px;
      max-height: 44px;
      text-decoration: inherit;
      letter-spacing: 0.1px;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      -o-text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      transition: 0.5s;

      &:hover {
        color: #461AAB;
      }
    }
  }

  &__col {
    display: flex;
    margin-bottom: 9px;
  }

  &__title {
    color: #464646;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__line {
    width: auto;
    flex: 1 1 auto;
    border-bottom: 1px dashed #E1D8D8;
  }

  &__count {
    color: #7339F5;
    text-align: right;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }

  &__link {
    margin-top: 47px;

    a {
      display: flex;
      align-items: center;

      &:hover {
        span {
          color: #461AAB;
          border-bottom: 1px solid #fff;
        }

        .arrow {
          background: #461AAB;
        }
      }

      span {
        color: #0B0B0B;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        border-bottom: 1px dashed #0B0B0B;
        transition: 0.5s;
        letter-spacing: 0.5px;
      }

      svg {
        width: 19.2px;
        height: 19.2px;
        fill: #fff;
      }

      .arrow {
        width: 24px;
        height: 24px;
        background: #7339F5;
        border-radius: 50%;
        margin-left: 11px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.5s;
      }
    }
  }
}